// peerConnection.js
import Peer from 'peerjs';
import { fetchTurnCredentials } from './client';

let peer = null;
let connections = {};
let handleTextMessageCallback;
let handleDataMessageCallback;

const iceServers = await fetchTurnCredentials();

export const initializePeer = (peerId, setWsConnected, setReadyToCommunicate) => {
    peer = new Peer(peerId, {
        host: 'api.securecloudgroup.com',
        port: '443',
        path: '/peerjs',
        secure: true, // if you are using HTTPS
        config: { iceServers }
    });

    peer.on('open', id => {
        console.log('PeerJS connection opened with ID:', id);
        setWsConnected(true);
    });

    peer.on('connection', conn => {
        connections[conn.peer] = conn;
        conn.on('data', handleMessage);
        conn.on('open', () => setReadyToCommunicate(true));
        conn.on('close', () => setReadyToCommunicate(false));
    });

    peer.on('error', err => {
        console.error('PeerJS error:', err);
        setWsConnected(false);
    });
};

export const connectToPeer = (targetPeerId, setReadyToCommunicate) => {
    const conn = peer.connect(targetPeerId);
    connections[targetPeerId] = conn;
    conn.on('data', handleMessage);
    conn.on('open', () => setReadyToCommunicate(true));
    conn.on('close', () => setReadyToCommunicate(false));
};

export const sendMessage = (targetPeerId, message) => {
    const conn = connections[targetPeerId];
    if (conn && conn.open) {
        conn.send(message);
        console.log('Sent message:', message);
    } else {
        console.error('Connection not open for peer:', targetPeerId);
    }
};

const handleMessage = (data) => {
    console.log('Received message:', data);
    if (data.type === 'text') {
        handleTextMessageCallback(data.content);
    } else if (data.type === 'data') {
        handleDataMessageCallback(data.content);
    }
};

export const setHandleTextMessageCallback = (callback) => {
    handleTextMessageCallback = callback;
};

export const setHandleDataMessageCallback = (callback) => {
    handleDataMessageCallback = callback;
};
